.ng2-c-view {
  box-sizing: border-box;
  display: block;
  height: 100%; }

.ng2-c-view__wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between; }

.ng2-c-view__content {
  box-sizing: border-box;
  flex-grow: 1;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll; }

.body--is-ios.body--backdrop-level-1 .ng2-c-view__content, .body--is-ios.body--backdrop-level-2 .ng2-c-view__content {
  -webkit-overflow-scrolling: auto; }

.ng2-c-view__content--center {
  align-items: center;
  display: flex;
  justify-content: center; }

.ng2-c-view--has-header {
  padding-top: 50px; }

.ng2-c-view--has-sidebar .ng2-c-view__content,
.ng2-c-view--has-sidebar .ng2-c-header {
  align-items: stretch;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  width: 100%; }

.ng2-c-view--has-sidebar .ng2-c-view__content {
  height: 100%; }

.ng2-c-view--has-sidebar .ng2-c-view__main,
.ng2-c-view--has-sidebar .ng2-c-view__sidebar {
  overflow: auto; }

.body--is-ios .ng2-c-view__statusbar {
  height: 20px;
  height: constant(safe-area-inset-top);
  height: env(safe-area-inset-top); }

.body--is-ios .ng2-c-header--sticky {
  top: 20px;
  top: constant(safe-area-inset-top);
  top: env(safe-area-inset-top); }

.body--is-ios .ng2-c-view--has-header {
  padding-top: calc(50px + 20px);
  padding-top: calc(50px + constant(safe-area-inset-top));
  padding-top: calc(50px + env(safe-area-inset-top)); }

.ng2-c-view__sidebar {
  background-color: #ffffff;
  display: block;
  min-width: 100%;
  overflow: auto;
  position: relative;
  transform: translateX(0);
  z-index: 1; }
  @media (min-width: 768px) {
    .ng2-c-view__sidebar {
      min-width: 280px;
      width: 280px; } }

@media (min-width: 768px) {
  .ng2-c-view__sidebar--wide {
    min-width: 320px;
    width: 320px; } }

.ng2-c-view__main {
  background: #f5f5f5;
  flex: 1;
  min-width: 100%;
  overflow-y: scroll;
  position: relative;
  transform: translateX(0);
  z-index: 1; }
  @media (min-width: 768px) {
    .ng2-c-view__main {
      border-left: 1px solid #e6e6e6;
      min-width: 0; } }

.ng2-c-view__main--padded {
  padding: 15px 10px; }
  @media (min-width: 768px) {
    .ng2-c-view__main--padded {
      padding: 30px; } }

.ng2-c-view__sidebar,
.ng2-c-view__main {
  box-sizing: border-box;
  transition-duration: 0.5s;
  transition-property: transform;
  transition-timing-function: ease-in-out; }

@media (max-width: 767px) {
  .ng2-c-view--sidebar-closed .ng2-c-view__main,
  .ng2-c-view--sidebar-closed .ng2-c-view__sidebar {
    transform: translateX(-100%); }
  .ng2-c-view--sidebar-closed .ng2-c-view__sidebar::after {
    bottom: 0;
    content: '';
    left: 0;
    position: fixed;
    right: 0;
    top: 0; } }

.ng2-c-split-view__content--center {
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  position: absolute;
  text-align: center;
  top: 0;
  width: 100%; }

.ng2-c-view__footer {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  flex-shrink: 0;
  min-height: 50px; }

.ng2-c-view__footer--center {
  justify-content: center; }

.ng2-c-view__footer-icon {
  color: rgba(255, 255, 255, 0.5);
  font-size: 28px; }

.ng2-c-view__statusbar {
  background-image: linear-gradient(to right, #28648a, #004160);
  background-repeat: no-repeat;
  background-size: cover;
  height: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 161; }
